import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './libs/router'
import store from './libs/store'

import { WebMIDI } from '../public/scripts/midiconnection.js';
import { BLEMIDIUtils } from '../public/scripts/blemidiutils.js';
import { EWI4000sDriver } from '../public/scripts/ewi4000sdriver.js';

Vue.config.productionTip = false

import Vuex from 'vuex'
Vue.use(Vuex)

Vue.mixin({
  store: function() {
    return store
  },
  data: function() {
    return {
      globalVar: {
        libs:{
          wm: new WebMIDI(),
          ewi4ksd: new EWI4000sDriver(),
          bleUtil: new BLEMIDIUtils()
        },
        config: {
          midi_deveice: {
            input: null,
            output: null
          },
        }
      }
    }
  },
})

new Vue({
  vuetify,
  router,
  render: h => h(App),
  created: function() {}
}).$mount('#app')

